@mixin hover() {
    @media (hover) {
        &:hover,
        &:focus {
            @content;
        }
    }
}

@mixin focus() {
    @media (hover) {
        &:focus {
            @content;
        }
    }
}

@mixin active {
    &:active {
        opacity: 0.7;
    }
}

@mixin media-xxx {
    @media (max-width: 1720px) {
        @content;
    }
}

@mixin media-max {
    @media (max-width: 1500px) {
        @content;
    }
}

@mixin media-content {
    @media (max-width: 1300px) {
        @content;
    }
}

@mixin media-xl {
    @media (max-width: 1200px) {
        @content;
    }
}

@mixin media-lg {
    @media (max-width: 1024px) {
        @content;
    }
}

@mixin media-md {
    @media (max-width: 980px) {
        @content;
    }
}

@mixin media-exsm {
    @media (max-width: 768px) {
        @content;
    }
}

@mixin media-sm {
    @media (max-width: 576px) {
        @content;
    }
}

@mixin mob {
    @media (max-width: 320px) {
        @content;
    }
}

@mixin landscape() {
    @media screen and (min-width: 480px) and (orientation: landscape) and (max-height: 420px) and (max-width: 900px) {
        @content;
    }
}

@mixin content {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}
